:root {
  --footer-text-color: #fff;
  --font-family: 'Epilogue', sans-serif;
  --footer-header-background-color: #1e2827;
  --trasform: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

/*--syles canceling--*/
h1,
h2,
h3,
h4,
h5,
p {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}
/*--MOBILE--*/
.footer-container {
  background-color: var(--footer-header-background-color);
  font-family: var(--font-family);
  list-style-type: none;
  color: var(--footer-text-color);
}
.footer-section {
  padding: 40px 20px 20px 20px;
  width: 100%;
}
.first-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}
.footer-logo {
  width: fit-content;
  display: flex;
  flex-direction: column;
}
.footer-logo-svg {
  width: 40px;
  height: 40px;
}

.tel-class-footer {
  padding-top: 40px;
}
.f-section-link {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  text-align: left;
  display: flex;
  flex-direction: row;
  width: max-content;
  justify-content: end;
  align-content: center;

  li {
    padding-bottom: 14px;
  }
  a {
    text-decoration: none;
    text-transform: uppercase;
    color: var(--footer-text-color);
  }
}
.address-footer {
  display: block;
  padding-top: 20px;
}
.social-links-footer {
  padding-top: 40px;
  padding-bottom: 40px;
}
.social-ellipse {
  display: inline-block;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  fill: var(--footer-header-background-color);
  border-radius: 10%;
  padding-right: 14px;
}
.social-icon {
  text-decoration: none;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #5e6265;
  background-color: var(--footer-header-background-color);
  border-radius: 20%;
  fill: var(--footer-text-color);
}
.icon {
  display: block;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  padding: 8px;
  svg {
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
  }
  use {
    width: 24px;
    height: 24px;
  }
}
.footer-policy-terms {
  display: flex;
  flex-direction: row;
  gap: 14px;
  padding-top: 14px;
}
.last-row {
  color: #c6cdd1;
}
/*--TABLET--*/
@media screen and (min-width: 768px) {
  .footer-container {
    height: 476px;
  }
  .footer-section {
    padding: 80px 32px 20px 32px;
  }
  .first-row-tablet {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .first-row {
    display: flex;
    justify-content: space-evenly;
    gap: 142px;
  }
  .social-links-footer {
    padding-top: 0;
  }
  .footer-policy-terms {
    padding: 0;
  }
  .last-row {
    display: flex;
    align-content: flex-end;
    justify-content: space-between;
    padding-top: 152px;
  }
  .social-ellipse {
    padding-right: 16px;
  }
}

/*--DESCKTOP--*/
@media screen and (min-width: 1158px) {
  .footer-section {
    padding: 104px 140px 32px 132px;
    width: 100%;
  }
  .first-row {
    width: 55%;
    align-items: flex-start;
    justify-content: space-between;
  }
}
